            let {encryptedData,iv }= detail;
            console.log(`调试:获取手机号调试`, encryptedData)
            await  getMobile({encryptedData,iv });

        },
        async handelGetUserInfo(res) {
            const { detail } = res;
            const { userInfo,encryptedData,iv } = detail;
            let token = await  Login();
            this.loading = false
            uni.redirectTo({
                url: '../index/index'
            })

        }
    }
}
